import axios from 'axios';
import {
  Message,
  MessageBox
} from 'element-ui';
import store from '../store';
import {
  getToken
} from '@/utils/auth';

// axios
const service = axios.create({
  baseURL: process.env.NODE_ENV === 'development' ?
    'https://www.easy-mock.com/mock/5c7794e135c5e14db980feed/admin' : process.env.VUE_APP_CURRENTMODE ===
    'uat' ? 'https://www.easy-mock.com/mock/5c7794e135c5e14db980feed/admin' : 'https://www.easy-mock.com/mock/5c7794e135c5e14db980feed/admin', // api  baseUrl
  timeout: 10000 // 
});

const timeOut = 10; //  
// request
service.interceptors.request.use(
  (config) => {
    if (store.getters.token) {
      config.headers['X-Token'] = getToken(); // token 
    }
    return config;
  },
  (error) => {
    // Do something with request error
    console.log(error); // for debug
    Promise.reject(new Error(error));
  }
);

// response 
service.interceptors.response.use(
  (response) => {
    /**
     * code20000 
     */
    const res = response.data;
    if (res.code !== 20000) {
      Message({
        message: res.message,
        type: 'error',
        duration: timeOut * 1000
      });

      // 50008:token; 50012:;  50014:Token ;
      if (res.code === 50008 || res.code === 50012 || res.code === 50014) {
        MessageBox.confirm(
          '',
          '', {
            confirmButtonText: '',
            cancelButtonText: '',
            type: 'warning'
          }
        )
          .then(() => {
            store.dispatch('FedLogOut')
              .then(() => {
                location.reload(); // vue-router bug
              });
          });
      }
      return Promise.reject(new Error(error));
    }
    if (process.env.NODE_ENV === 'development') {
      return response
    }
    return response.data;
  },
  (error) => {
    console.log('err' + error); // for debug
    Message({
      message: error.message,
      type: 'error',
      duration: timeOut * 1000
    });
    return Promise.reject(new Error(error));
  }
);

export default service;
